import data from './test.js'

addChapter(data, [2, 3, 7, 5])

// 获取每个题目
function addChapter(data, NumberOfQuestions){
    let parent = document.createElement('div')
    let template, ulContent, title = ''
    let ques = NumberOfQuestions
    
    data.forEach( (item,index) => {
        // 清空之前的内容
        ulContent = ''
        title = data[index].title
        let problemList = randomArray(data[index].text, ques[index]).result
        problemList.forEach(text => {
            ulContent+=`<li>${text}</li>`
        })
        
        template = `
        <div class="part">
            <h2>${title}</h2>
            <ol>${ulContent}</ol>
        </div>
        `
        parent.appendChild(changeDom(template))
        
    })
    get_el('#app').appendChild(parent)
}

// 获取元素
function get_el(element){
    return document.querySelector(element)
}

// 将字符串转换为DOM元素
function changeDom(element){
    let div = document.createElement('div')
    div.innerHTML = element
    let span = div.firstElementChild
    return span
}

// 随机抽取题目
function randomArray(array, number){
    let isArray = Array.from(array)
    let result = []
    for(let i = 0; i < number; i++){
        let single = Math.floor(Math.random() * isArray.length)
        result.push(isArray.splice(single, 1)[0])
    }
    return {
        result: result,
        after: isArray
    }
}

// 根据存储名称获取 localStorage 的值
function getLocal(key){
    return JSON.parse(localStorage.getItem(key))
}

// 设置/存储 localStorage 的值
function setLocal(key, data){
    localStorage.setItem(key, JSON.stringify(data))
}

